import React, {useState} from 'react';

//     
interface UserFormState {
    username: string;
    email: string;
    password: string;
    confirmPassword: string;
    errors: {
        username?: string;
        email?: string;
        password?: string;
        confirmPassword?: string;
    };
}

const initialFormState: UserFormState = {
    username: '',
    email: '',
    password: '',
    confirmPassword: '',
    errors: {},
};

export const RegistrationForm = () => {
    
    const [formState, setFormState] = useState<UserFormState>(initialFormState);

    //        
    const handleInputChange = (field: keyof UserFormState, value: string) => {
        setFormState({
            ...formState,
            [field]: value,
        });
    };

    //      
    const validateForm = () => {
        let errors: UserFormState['errors'] = {};

        if (formState.username.length < 3) {
            errors.username = '      3 ';
        }
        //  ,   ..
        // ...

        setFormState({
            ...formState,
            errors: errors,
        });

        return Object.keys(errors).length === 0;
    };

    // ,    
    const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        //   
        if (validateForm()) {
            console.log(' ', formState);
            //       ...
        }
    };

    return (
        <form onSubmit={handleSubmit}>
            <div>
                <label htmlFor="username"> :</label>
                <input
                    id="username"
                    type="text"
                    value={formState.username}
                    onChange={(e) => handleInputChange('username', e.target.value)}
                />
                {formState.errors.username && <p>{formState.errors.username}</p>}
            </div>

            {/*   email, password  confirmPassword    */}
            {/* ... */}

            <button type="submit"></button>
        </form>
    );
};
